import React from 'react';
import getClassName from '../../helpers/getClassName';
import classnames from '../../lib/classnames';
import PropTypes from 'prop-types';
import ReactDOM from 'react-dom';
import Icon16Cancel from '../../../dist/icons/16/cancel';
import './BottomModal.css';

const baseClassName = getClassName('BottomModal');

export default class BottomModal extends React.Component {

  state = {
    fadeIn: false,
    fadeOut: false
  };

  static propTypes = {
    className: PropTypes.string,
    title: PropTypes.node,
    children: PropTypes.node,
    onClose: PropTypes.func,
    addon: PropTypes.node
  };

  componentWillMount () {
    this.node = document.createElement('div');
    document.body.appendChild(this.node);
  }

  componentDidMount () {
    this.setState({ fadeIn: true });
  }

  componentWillUnmount () {
    document.body.removeChild(this.node);
  }

  onClose = () => {
    this.setState({ fadeOut: true });
    this.containerEl.addEventListener('transitionend', this.props.onClose);
  };

  containerRef = el => this.containerEl = el;

  render () {
    const { className, title, addon, children } = this.props;

    return (ReactDOM.createPortal(
      <div className={classnames(baseClassName, className, {
        'BottomModal--fade-in': this.state.fadeIn,
        'BottomModal--fade-out': this.state.fadeOut
      })}>
        <div className="BottomModal__container" ref={this.containerRef}>
          <div className="BottomModal__header">
            <div className="BottomModal__addon">
              {addon}
            </div>
            {title && <div className="BottomModal__title">{title}</div>}
            <div className="BottomModal__close" onClick={this.onClose}>
              <Icon16Cancel/>
            </div>
          </div>
          <div className="Bottom__content">
            {children}
          </div>
        </div>
      </div>, this.node)
    );
  }
}



